.home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    background : linear-gradient(
        to bottom, rgba(0,0,0,0) rgba(0,0,0,1)
    );
}

.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
    background-color: black;
}

.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}